






















import Vue from 'vue';
import NumberPad from '@/components/Ledger/NumberPad.vue';
import FormItem from '@/components/Ledger/FormItem.vue';
import Tags from '@/components/Ledger/Tags.vue';
import {Component} from 'vue-property-decorator';
import recordTypeList from '@/constant/recordTypeList';
import Tabs from '@/components/Tabs.vue';

@Component({components: {Tabs, Tags, FormItem, NumberPad}})
export default class Ledger extends Vue{
  get recordList(){
    return this.$store.state.recordList
  }
  recordTypeList = recordTypeList
  record: RecordItem = {
    tags:[], notes:'', type:'-', amount: 0, createdAt: new Date().toISOString()
  }
  created(){
    this.$store.commit('fetchRecords')
  }
  saveRecord(tag: Tag){
    if(!this.record.tags || this.record.tags.length === 0) {
     return window.alert('请选择标签')
    }
    if(this.record.amount === 0){return window.alert('请填写金额')}
    this.$store.commit('createRecord', this.record)
    const index = this.record.tags.indexOf(tag);
    this.record.tags.splice(index,1)
    this.record.amount = 0
    this.record.notes = ''
    this.record.createdAt = new Date().toISOString()
  }
}
