



















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import FormItem from '@/components/Ledger/FormItem.vue';
import Button from '@/components/Button.vue';

@Component({components: {Button, FormItem}})
export default class EditLabel extends Vue {
  // ② 第二步通过 set 函数来设置
  get currentTag(){
    return this.$store.state.currentTag
  }

  created(){
    const id = this.$route.params.id
    this.$store.commit('fetchTags')
    // 这里想要获取当前：currentTag，发现 commit 没有返回值 没办法获取
    this.$store.commit('setCurrentTag', id) // ③ 第三步 在这里读取 currentTag
    if(!this.currentTag){
      this.$router.replace('/404')
    }
  }
  update(name: string){
    if(this.currentTag) {
      this.$store.commit('updateTag',{id:this.currentTag.id, name: name})
    }
  }
  remove() {
    if (this.currentTag)
    this.$store.commit('removeTag',this.currentTag.id)
  }
  goBack(){
    if (this.currentTag.name === '') {
      return window.alert('标签名不能为空');
    }
    this.$router.back();
  }
}
